import Swiper from "swiper";
import { Navigation } from "swiper/modules";
import "swiper/css";

export function initSwiper() {
  const swiperProd = new Swiper(".item-card__item-swiper", {
    modules: [Navigation],
    loop: true,
    grabCursor: true,
    slidesPerView: 1,
    speed: 800,

    navigation: {
      nextEl: ".item-card__item-btn_next",
      prevEl: ".item-card__item-btn_prev",
    },
  });
}
